import React from 'react';
import './Register.css';
import { useFormik } from 'formik';
import * as Yup from 'yup';
import userAPI from '../../apis/userAPI';
import { errorToast, successToast } from './../../components/Toasts/Toasts';

const Register = () => {

  const registerFormik = useFormik({
    initialValues: {
      inputUsername: '',
      inputEmail: '',
      inputToken: '',
      inputPassword: '',
      inputRePassword: ''
    },
    validationSchema: Yup.object({
      inputUsername: Yup.string()
        .required("Bt buc nhp h tn !"),
      inputEmail: Yup.string()
        .required("Bt buc nhp email !")
        .email("Khng ng nh dng email !"),
      inputPassword: Yup.string()
        .required("Bt buc nhp mt khu !")
        .min(6, "Mt khu ngn nht l 6 k t !")
        .max(30, "Mt khu di nht l 30 k t"),
      inputRePassword: Yup.string()
        .required("Bt buc li nhp mt khu !")
        .min(6, "Mt khu ngn nht l 6 k t !")
        .max(30, "Mt khu di nht l 30 k t")
        .oneOf([Yup.ref("inputPassword")], "Mt khu phi trng nhau !")
    }),
    onSubmit: (values, { resetForm }) => {
      let data = {
        username: values.inputUsername,
        email: values.inputEmail,
        password: values.inputPassword,
      };

      userAPI.register(data).then(res => {
        if (res.data.message === 'EMAIL_EXISTS') {
          errorToast("Email  c s dng, chn email khc !");
        }
        if (res.data.message === 'SUCCESS') {
          successToast("ng k ti khon thnh cng, vui lng vo email  kch hot ti khon !");
          resetForm({ values: '' });
        }
        if (res.data.message === 'FAILED_SEND_EMAIL') {
          errorToast("Gi mail khng thnh cng do li bo mt ca google !");
        }
      }).catch(err => {
        errorToast("C li xy ra, vui lng th li !");
      });
    }
  });

  return (
    <div className="modal fade mt-5" id="formdangky" data-backdrop="static" tabIndex={-1} aria-labelledby="dangky_tieude" aria-hidden="true">
      <div className="modal-dialog modal-lg">
        <div className="modal-content">
          <div className="modal-header">
            <ul className="tabs d-flex justify-content-around list-unstyled mb-0">
              <li className="tab tab-dangnhap text-center">
                <a className="text-decoration-none" href="# ">ng nhp</a>
                <hr />
              </li>
              <li className="tab tab-dangky text-center">
                <a className="text-decoration-none" href="# " >ng k</a>
                <hr />
              </li>
            </ul>
            <button type="button" className="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true"></span>
            </button>
          </div>
          <div className="modal-body">
            <form id="form-signup" className="form-signin mt-2" onSubmit={registerFormik.handleSubmit}>

              <div className="mb-3">
                <div className="form-group">
                  <label htmlFor="inputUsername">H tn (*)</label>
                  <input type="text" className="form-control" placeholder="Nhp h v tn..." name="inputUsername"
                    value={registerFormik.values.inputUsername}
                    onChange={registerFormik.handleChange}
                  />
                  {registerFormik.errors.inputUsername && registerFormik.touched.inputUsername && (
                    <small className="active-error" >{registerFormik.errors.inputUsername}</small>
                  )}
                </div>
              </div>

              <div className="mb-3">
                <div className="form-group">
                  <label htmlFor="inputEmail">Email (*)</label>
                  <input type="text" className="form-control" placeholder="Nhp a ch email..." name="inputEmail"
                    value={registerFormik.values.inputEmail}
                    onChange={registerFormik.handleChange}
                  />
                  {registerFormik.errors.inputEmail && registerFormik.touched.inputEmail && (
                    <small className="active-error" >{registerFormik.errors.inputEmail}</small>
                  )}
                </div>
              </div>

              <div className="mb-3">
                <div className="form-group">
                  <label htmlFor="inputPassword">Mt khu (*) </label>
                  <input type="password" className="form-control" placeholder="Nhp mt khu..." name="inputPassword"
                    value={registerFormik.values.inputPassword}
                    onChange={registerFormik.handleChange}
                  />
                  {registerFormik.errors.inputPassword && registerFormik.touched.inputPassword && (
                    <small className="active-error" >{registerFormik.errors.inputPassword}</small>
                  )}
                </div>
              </div>

              <div className="mb-3">
                <div className="form-group">
                  <label htmlFor="inputRePassword">Nhp li mt khu (*) </label>
                  <input type="password" className="form-control" placeholder="Nhp li mt khu..." name="inputRePassword"
                    value={registerFormik.values.inputRePassword}
                    onChange={registerFormik.handleChange}
                  />
                  {registerFormik.errors.inputRePassword && registerFormik.touched.inputRePassword && (
                    <small className="active-error" >{registerFormik.errors.inputRePassword}</small>
                  )}
                </div>
              </div>

              <button className="btn btn-lg btn-block btn-signin text-uppercase text-white mt-3" type="submit" style={{ background: '#F5A623' }}>ng k</button>
              <hr className="mt-3 mb-2" />
              <div className="custom-control custom-checkbox">
                <p className="text-center">Bng vic ng k, bn  ng  vi DeLuCa v</p>
                <a href="# " className="text-decoration-none text-center" style={{ color: '#F5A623' }}>iu khon dch
                  v &amp; Chnh sch bo mt</a>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Register;
