import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import './Account.css';
import TabInfo from './TabInfo';
import userAPI from './../../apis/userAPI';
import getCookie from '../../utils/getCookie';
import TabUpdatePass from './TabUpdatePass';

const Account = () => {
  const [userInfo, setUserInfo] = useState({});
  useEffect(() => {
    userAPI.getUserById(getCookie('currentUserId')).then((res) => {
      setUserInfo(res.data.data);
    }).catch(err => {
      console.log(err);
    })
    window.scrollTo(0, 0);
  }, []);
  return (
    <>
      <section className="breadcrumbbar">
        <div className="container">
          <ol className="breadcrumb mb-0 p-0 bg-transparent">
            <li className="breadcrumb-item"><Link to="/" >Trang ch</Link></li>
            <li className="breadcrumb-item active"><a href="# ">Thng tin ti khon</a></li>
          </ol>
        </div>
      </section>
      <section className="account-page">
        <div className="container">
          <div className="page-content bg-white">
            <div className="account-page-tab-content m-4">
              {/* 2 tab: thng tin ti khon, danh sch n hng  */}
              <nav>
                <div className="nav nav-tabs" id="nav-tab" role="tablist">
                  <a className="nav-item nav-link active" id="nav-taikhoan-tab" data-toggle="tab" href="#nav-taikhoan" role="tab" aria-controls="nav-home" aria-selected="true">Thng tin ti khon</a>
                  <a className="nav-item nav-link" id="nav-password-tab" data-toggle="tab" href="#nav-password" role="tab" aria-controls="nav-profile" aria-selected="false">Cp nht mt khu</a>
                </div>
              </nav>
              {/* ni dung 2 tab */}
              <div className="tab-content">
                {/* ni dung tab 1: thng tin ti khon  */}
                <TabInfo
                  userInfo={userInfo}
                />

                {/* ni dung tab 2: Cp nht mt khu */}
                <TabUpdatePass />

              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  )
}

export default Account;
