import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import './TrackingOrder.css';
import orderAPI from './../../apis/orderAPI';
import formatDate from './../../utils/formatDate';

const TrackingOrder = (props) => {
  const [order, setOrder] = useState({});

  useEffect(() => {
    orderAPI.getOrderDetailByCode(props.match.params.code).then((res) => {
      setOrder(res.data.data.order);
      console.log(res.data.data.order);
    }).catch((err) => {
      console.log(err);
    })

  }, [props.match.params.code]);

  let tracking = (status) => {
    if (status === 't hng thnh cng') {
      return (
        <>
          <div className="step active"> <span className="icon"> <i className="fa fa-check"></i> </span> <span className="text">t hng thnh cng</span> </div>
          <div className="step"> <span className="icon"> <i className="fa fa-user"></i> </span> <span className="text"> Tip nhn n hng</span> </div>
          <div className="step"> <span className="icon"> <i className="fa fa-truck"></i> </span> <span className="text">ang giao hng </span> </div>
          <div className="step"> <span className="icon"> <i className="fa fa-box"></i> </span> <span className="text">Giao hng thnh cng</span> </div>
        </>
      )
    }
    if (status === 'Tip nhn') {
      return (
        <>
          <div className="step active"> <span className="icon"> <i className="fa fa-check"></i> </span> <span className="text">t hng thnh cng</span> </div>
          <div className="step active"> <span className="icon"> <i className="fa fa-user"></i> </span> <span className="text"> Tip nhn n hng</span> </div>
          <div className="step"> <span className="icon"> <i className="fa fa-truck"></i> </span> <span className="text">ang giao hng </span> </div>
          <div className="step"> <span className="icon"> <i className="fa fa-box"></i> </span> <span className="text">Giao hng thnh cng</span> </div>
        </>
      )
    }
    if (status === 'ang giao hng') {
      return (
        <>
          <div className="step active"> <span className="icon"> <i className="fa fa-check"></i> </span> <span className="text">t hng thnh cng</span> </div>
          <div className="step active"> <span className="icon"> <i className="fa fa-user"></i> </span> <span className="text"> Tip nhn n hng</span> </div>
          <div className="step active"> <span className="icon"> <i className="fa fa-truck"></i> </span> <span className="text">ang giao hng </span> </div>
          <div className="step"> <span className="icon"> <i className="fa fa-box"></i> </span> <span className="text">Giao hng thnh cng</span> </div>
        </>
      )
    }
    if (status === ' giao hng') {
      return (
        <>
          <div className="step active"> <span className="icon"> <i className="fa fa-check"></i> </span> <span className="text">t hng thnh cng</span> </div>
          <div className="step active"> <span className="icon"> <i className="fa fa-user"></i> </span> <span className="text"> Tip nhn n hng</span> </div>
          <div className="step active"> <span className="icon"> <i className="fa fa-truck"></i> </span> <span className="text">ang giao hng </span> </div>
          <div className="step active"> <span className="icon"> <i className="fa fa-box"></i> </span> <span className="text">Giao hng thnh cng</span> </div>
        </>
      )
    }
  }

  return (
    <>
      <section className="breadcrumbbar">
        <div className="container">
          <ol className="breadcrumb mb-0 p-0 bg-transparent">
            <li className="breadcrumb-item"><Link to="/" >Trang ch</Link></li>
            <li className="breadcrumb-item"><Link to="/orders/history">Qun l n hng</Link></li>
            <li className="breadcrumb-item"><Link to={`/orders/view/${props.match.params.code}`} >Chi tit n hng #{props.match.params.code}</Link></li>
            <li className="breadcrumb-item active"><a href="# " >Theo di n hng #{props.match.params.code}</a></li>
          </ol>
        </div>
      </section>

      <section className="content">
        <div className="container">
          <div className="row">
            <div className="col-12">
              <div className="title">THNG TIN TRNG THI N HNG</div>
              <div className="content-receiver">
                <h6>Trng thi: {order.o_status} | {order.updatedAt ? formatDate(order.updatedAt) : formatDate(order.createdAt)} </h6>
                <div className="track">
                  {tracking(order.o_status) }
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  )
}

export default TrackingOrder;
