import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import orderAPI from '../../apis/orderAPI';

import formatDate from './../../utils/formatDate';
import { errorToast, successToast } from './../../components/Toasts/Toasts';

const Order = () => {
  const [orders, setOders] = useState([]);
  useEffect(() => {
    orderAPI.getAllOrderOfUser().then(res => {
      if (res.data.message === 'SUCCESS') {
        setOders(res.data.data);
        console.log(res.data.data);
      }
    }).catch(err => {
      console.log(err);
    });
    window.scrollTo(0, 0);
  }, []);

  let destroyOrder = (id) => {
    orderAPI.destroyOrderById(id).then((res) => {
      if (res.data.message === 'NOT_FOUND') {
        errorToast("n hng khng tn ti")
      }
      if (res.data.message === 'SUCCESS') {
        successToast("Hy n hng thnh cng");
      }
    }).catch(err => {
      errorToast("C li xy ra, vui lng th li sau")
    })
  }

  return (
    <>
      <section className="breadcrumbbar">
        <div className="container">
          <ol className="breadcrumb mb-0 p-0 bg-transparent">
            <li className="breadcrumb-item"><Link to="/" >Trang ch</Link></li>
            <li className="breadcrumb-item active"><a href="# ">Qun l n hng</a></li>
          </ol>
        </div>
      </section>
      <section className="content">
        <div className="container">
          <table className="table table-hover">
            <thead style={{ background: '#dee2e9' }}>
              <tr>
                <th>M n hng</th>
                <th>Ngy mua</th>
                <th>Tng tin</th>
                <th>Trng thi n hng</th>
                <th>Hnh ng</th>
              </tr>
            </thead>
            <tbody>
              {
                orders.map((v, i) => {
                  return (
                    <tr key={i}>
                      <td>
                        <Link to={`/orders/view/${v.o_code}`} >  {v.o_code} </Link>
                      </td>
                      <td> {formatDate(v.createdAt)} </td>
                      <td>{new Intl.NumberFormat('vi-VN', {
                        minimumFractionDigits: 0,
                        maximumFractionDigits: 0
                      }).format(v.o_totalPrice)} </td>
                      <td>{v.o_status}</td>
                      <td>
                        {
                          v.o_status === " hy" ? (` hy lc: ` + formatDate(v.updatedAt)) :
                            (v.o_status === "ang giao hng" || v.o_status === " giao hng" ? '' :
                              (<button className="btn btn-danger" onClick={() => destroyOrder(v._id)}>
                                <i className="fas fa-trash-alt mr-1"></i>
                                Hy n hng </button>))
                        }
                      </td>
                    </tr>
                  )
                })
              }
            </tbody>
          </table>
        </div>
      </section>
    </>
  )
}

export default Order;
