import React from 'react';
import * as Yup from 'yup';
import { useFormik } from 'formik';
import userAPI from '../../apis/userAPI';
import { errorToast, successToast } from '../Toasts/Toasts';

const ForgotPassword = () => {
  let forgotPassFormik = useFormik({
    initialValues: {
      email: ''
    },
    validationSchema: Yup.object({
      email: Yup.string()
        .required("Bt buc nhp email !")
        .email("Khng ng nh dng email")
    }),
    onSubmit: (values, { resetForm }) => {
      userAPI.forgotPassword(values).then(res => {
        if (res.data.message === 'USER_NOT_FOUND') {
          errorToast("Ngi dng khng tn ti !");
        }
        if (res.data.message === 'FAILED_SEND_EMAIL') {
          errorToast("Gi mail li !");
        }
        if (res.data.message === 'SUCCESS') {
          successToast("Mt khu mi  c gi vo mail ca bn !");
          resetForm({ values: ''});
        }
      }).catch(err => {
        errorToast("C li xy ra, vui lng th li sau !");
      })
    }
  });
  return (
    <form onSubmit={forgotPassFormik.handleSubmit}>
      <div className="mb-3">
        <div className="input-group">
          <input type="email" className="form-control" name="email" placeholder="Nhp a ch email khi phc mt khu..." aria-label="Recipient's username" aria-describedby="basic-addon2"
            value={forgotPassFormik.values.email}
            onChange={forgotPassFormik.handleChange}
          />
          <div className="input-group-append">
            <button className="btn btn-outline-warning" type="submit">Gi</button>
          </div>
        </div>
        {forgotPassFormik.errors.email && forgotPassFormik.touched.email && (
          <small style={{ color: 'red' }}>{forgotPassFormik.errors.email}</small>
        )}
      </div>
    </form>
  )
}

export default ForgotPassword
